
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import {detail, wallet} from '../config'
import VScroll from '../components/VScroll'
export default {
  name: 'detail',
  head: {
    title: {
      inner: '累计收入明细'
    }
  },
  components: {
    Group,
    Cell,
    VScroll
  },
  data () {
    return {
      balance: 0,
      form: {
        userId: '',
        type: 1,
        limit: 10,
        pageIndex: 0
      },
      list: []
    }
  },
  mounted () {
    this.form.userId = this.$route.params.userId
    if (this.form.userId === 'null') {
      this.$router.replace('/login')
    } else {
      this.getList(() => {}, null)
    }
  },
  methods: {
    onRefresh (done) {
      this.form.pageIndex = 0
      this.getList(done, 1)
    },
    onInfinite (done) {
      this.form.pageIndex ++
      this.getList(done, 0)
    },
    getList (done, status) {
      const This = this
      this.$http({
        method: 'jsonp',
        url: detail,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: this.form,
        before: () => {
          if (status) {
            this.list = []
          }
        }
      })
      .then(res => {
        console.log(res)
        res.body.data.scoreList.forEach(el => {
          This.list.push(el)
        })
        if (res.body.data.scoreList.length < This.form.limit) {
          this.$el.querySelector('.load-more').innerHTML = '我是有底线的！'
        }
        done()
        for (const i in this.list) {
          this.list[i].createTime = dateFormat(this.list[i].createTime)
        }
        this.$http({
          method: 'jsonp',
          url: wallet,
          jsonp: 'callback',
          jsonpCallback: 'json',
          params: {
            userId: this.form.userId
          }
        })
        .then(res => {
          console.log(res)
          this.balance = res.body.data.wallet.cumulative
        })
      })
    }
  }
}



